<template>
  <input
    id="file_selector"
    class="file_selector"
    type="file"
    accept="image/*"
    @change="preview()"
  >
</template>

<script>
import upload from '@/utils/cos'
export default {
  methods: {
    preview () {
      let file = this.$el.files[0]
      if (this.checkFileSize(file)) {
        if (this.isImg(file.name)) {
          this.upload(file)
        } else {
          this.$store
            .commit('showMessage', {
              message: '请选择图片文件'
            })
        }
      } else {
        this.$store
          .commit('showMessage', {
            message: '图片大小不能大于1M'
          })
      }
    },
    isImg (fileName) {
      if (fileName) {
        let suffixIndex = fileName.lastIndexOf('.')
        let suffix = fileName
          .substring(suffixIndex + 1, fileName.length)
          .toLowerCase()
        return /bmp|png|jpg|jpeg/.test(suffix)
      } else {
        return false
      }
    },
    checkFileSize (file) {
      return file.size <= 1 * 1024 * 1024
    },
    upload (file) {
      upload(file, {
        onReady: () => {
          this.$store.commit('updateLoadingStatus', {
            isLoading: true
          })
        },
        onSuccess: (data) => {
          this.$emit('on-success', `https://${data.Location}`)
        },
        onError: () => {
          this.$store.commit('updateLoadingStatus', {
            isLoading: false
          })
          this.$store
            .commit('showMessage', {
              message: '头像修改失败，请重试！'
            })
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
.file_selector {
  opacity: 0;
  position: absolute;
  width: 96px;
  top: 0;
  right: 0;
  bottom: 0;
  user-select: none;
}
</style>
