<template>
  <div class="settings">
    <van-cell-group v-if="userInfo">
      <van-cell
        title="头像"
        class="index-line"
      >
        <uploader @on-success="updateUserHead" />
        <avatar
          size="48px"
          :avatar="userInfo.HeadImage"
        />
      </van-cell>
      <van-cell
        title="昵称"
        @click="updateUserInfo('Name',userInfo.Name,'设置昵称')"
        is-link
        :value="userInfo.Name"
      />
      <van-cell
        title="手机号"
        :value="userInfo.UserName"
      />
      <!-- <van-cell
        title="地址"
        is-link
        @click="updateUserInfo('Address',userInfo.Address,'设置地址')"
        :value="userInfo.Address"
      /> -->
      <van-cell
        title="备注"
        :value="userInfo.Remark"
        is-link
        @click="updateUserInfo('Remark',userInfo.Remark,'设置备注')"
      />
    </van-cell-group>
    <div class="logout">
      <van-button
        size="large"
        @click="logout"
      >退出登录</van-button>
    </div>
  </div>
</template>

<script>
import Avatar from "@/components/Avatar";
import Uploader from "@/components/Uploader";
export default {
  components: { Avatar, Uploader },
  methods: {
    logout() {
      this.$store
        .dispatch("logout")
        .then(() => {
          this.back();
        })
        .catch(() => {});
    },
    updateUserInfo(key, value, title) {
      this.$router.push({
        name: "UserUpdate",
        query: {
          key,
          value,
          title
        }
      });
    },
    updateUserHead(headImage) {
      this.$store
        .dispatch("updateUserInfo", ["HeadImage", headImage])
        .then(() => {})
        .catch(() => {});
    }
  }
};
</script>

<style lang="less" scoped>
.avatar-content {
  float: right;
  clear: both;
}
.logout {
  padding-top: 40px;
  .van-button--large {
    width: 100%;
    height: 40px;
    line-height: 38px;
  }
}
</style>
